const default_state = {
    sum: 1
};
// Reducer1
const math = (state = default_state, action) => {
    switch (action.type) {
        case 'ADD': {
            return {
                sum: state.sum + action.payload
            };
        }
        case 'DEDUCE': {
            return {
                sum: state.sum - action.payload
            };
        }
        case 'MULTI': {
            return {
                sum: state.sum * action.payload
            };
        }
        default: {
            return state;
        }
    }
};
// 
const test_state = math({ sum: 1 }, {
    type: 'ADD',
    payload: 2
});
// Reducer2
const my_reducer_2 = (state = 0, action) => {
    switch (action.type) {
        case 'ADD': {
            return state + (action.payload) * 10;
        }
        case 'DEDUCE': {
            return state - (action.payload) * 10;
        }
        case 'MULTI': {
            return state * (action.payload) * 10;
        }
        default: {
            return state;
        }
    }
};
// Reducer3
const my_reducer_3 = (state = 100, action) => {
    switch (action.type) {
        case 'ADD': {
            return state + (action.payload) * 100;
        }
        case 'DEDUCE': {
            return state - (action.payload) * 100;
        }
        case 'MULTI': {
            return state * (action.payload) * 100;
        }
        default: {
            return state;
        }
    }
};
/**
 * 
 * ...my_reducer_list  key : value 
 */
exports.math = math;
exports.my_reducer_2 = my_reducer_2;
exports.my_reducer_3 = my_reducer_3;
